$animalBackground: #9b980f;
$bakeryBackground: #8d6025;
$drinkBackground: #2f7591;
$fastfoodBackground: #c76a00;
$inedibleBackground: #5f6274;
$meatBackground: #831f1f;
$milkBackground: #9fafac;
$otherBackground: #914882;
$plantBackground: #657259;
$sweetBackgriound: #7d279d;
$veganBackground: #3c8d55;

$animalText: #464514;
$bakeryText: #4a3112;
$drinkText: #1b4454;
$fastfoodText: #643d0f;
$inedibleText: #313340;
$meatText: #481b1b;
$milkText: #49625e;
$otherText: #681657;
$plantText: #3a4431;
$sweetText: #4b2659;
$veganText: #294833;

$animalTextShadow: gold;
$bakeryTextShadow: orange;
$drinkTextShadow: lightblue;
$fastfoodTextShadow: orange;
$inedibleTextShadow: lightgray;
$meatTextShadow: red;
$milkTextShadow: white;
$otherTextShadow: violet;
$plantTextShadow: khaki;
$sweetTextShadow: magenta;
$veganTextShadow: green;

$animalFilter: invert(14%) sepia(9%) saturate(3640%) hue-rotate(314deg)
  brightness(98%) contrast(97%) drop-shadow(0px 0px 2px $animalTextShadow);
$bakeryFilter: invert(14%) sepia(22%) saturate(5628%) hue-rotate(25deg)
  brightness(87%) contrast(86%) drop-shadow(0px 0px 2px $bakeryTextShadow);
$drinkFilter: invert(19%) sepia(62%) saturate(555%) hue-rotate(151deg)
  brightness(91%) contrast(87%) drop-shadow(0px 0px 2px $drinkTextShadow);
$fastfoodFilter: invert(20%) sepia(52%) saturate(934%) hue-rotate(356deg)
  brightness(101%) contrast(90%) drop-shadow(0px 0px 2px $fastfoodTextShadow);
$inedibleFilter: invert(17%) sepia(14%) saturate(764%) hue-rotate(195deg)
  brightness(96%) contrast(90%) drop-shadow(0px 0px 2px $inedibleTextShadow);
$meatFilter: invert(13%) sepia(28%) saturate(1950%) hue-rotate(321deg)
  brightness(86%) contrast(94%) drop-shadow(0px 0px 2px $meatTextShadow);
$milkFilter: invert(36%) sepia(22%) saturate(411%) hue-rotate(121deg)
  brightness(90%) contrast(84%) drop-shadow(0px 0px 2px $milkTextShadow);
$otherFilter: invert(13%) sepia(37%) saturate(4868%) hue-rotate(292deg)
  brightness(92%) contrast(96%) drop-shadow(0px 0px 2px $otherTextShadow);
$plantFilter: invert(20%) sepia(62%) saturate(287%) hue-rotate(87deg)
  brightness(100%) contrast(92%) drop-shadow(0px 0px 2px $plantTextShadow);
$sweetFilter: invert(14%) sepia(52%) saturate(1336%) hue-rotate(250deg)
  brightness(96%) contrast(91%) drop-shadow(0px 0px 2px $sweetTextShadow);
$veganFilter: invert(20%) sepia(62%) saturate(287%) hue-rotate(87deg)
  brightness(100%) contrast(92%) drop-shadow(0px 0px 2px $veganTextShadow);

$yellowFilter: invert(57%) sepia(10%) saturate(4683%) hue-rotate(25deg)
  brightness(96%) contrast(88%);
$cyanFilter: invert(35%) sepia(30%) saturate(419%) hue-rotate(119deg)
  brightness(93%) contrast(92%);

$darkPurple: #160026;
$madPurple: #2a093e;
$darkGreen: #002c30;
$green: #013740;
$cyan: #3e645d;
$pink: #8c00b9;
$madPink: #674c76;
